
.body{

    display: flex;
    justify-content: center;
    margin-top: 50px;

}
button{
  background-color: rgb(214, 132, 8);
  margin-right: 20px;
  border-radius: 10px;
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}
